
body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  font-family: -apple-system, BlinkMacSystemFont, "didot", serif,'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 1px;
  }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }