.portfolio {
    background-color: #E9EFF2;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;
    box-sizing: border-box;
}
.header-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.header {
    width: 100%;
    background-image: linear-gradient(#013440);
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    z-index: 100;
    padding: 10px 20px;
}

.menu-toggle {
  display: none;
  font-size: 2.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.logo{
    display: flex;
    justify-content: flex-start;
    height: 50px;
    padding-left: 10px;
    transition: transform 0.3s ease, opacity 0.3s ease;
  }

.logo img {
  height: 100%;
  max-height: 40px;
  width: auto;
  object-fit: contain;  
}

.logo:hover {
  transform: scale(1.05);
  opacity: 0.9;
}
  

.header ul {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    text-decoration: none;
    gap: 10px;
    }   
       
 .header li {
    line-height: 3rem;
    }
        
        
.header a {
    color: #E9EFF2;
    text-decoration: none;
    font-size: 1.4rem;
    font-family: "didot", serif;
    text-decoration: none;
    list-style: none;
    padding: 7px 13px;
    border-radius: 3px;
    text-transform: uppercase;
    transition: 0.3s;
}

.header a:hover {
    color: #c77986 ;
    transition: .3s;
}

.welcome {
    text-align: center;
    font-family: "didot", serif;
    color: #013440;
    align-items: center;
    padding: 40px 20px;
}

.welcome h1 {
    font-size: 2.8rem;
    font-weight: 700;
    line-height: 1.4;
    padding-bottom: 20px;
}

.welcome p {
    font-size: 1.6rem;
    line-height: 2.6rem;
    font-family: "didot", serif;
    color: #656c6d;
    font-weight: 600;
}

.my-projects {
    padding: 20px;
    text-align: center;
    justify-content: center;
    color: #656c6d;
    font-family: "didot", serif;
}

.h2 {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    gap: 1rem;
    font-weight: 600;
    margin-bottom: 20px;
}

.projects-grid {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    padding-bottom: 40px;
}

.project {
    background-color: #f8f9fa;
    padding: 5px;
    border-radius: 5px;
    width: 90%;
    max-width: 250px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    transition: transform 0.3s ease;
}

.project:hover {
    transform: translateY(-5px);
}

.project img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 100%;
  max-width: 250px;
  display: block;
  margin: 0 auto;
}

.project img:hover {
    box-shadow: 0 0 2px 1px rgba(101, 108, 109, 0.5);
  }

h3 {
    font-size: 1.6rem;
    font-weight: bold;
    margin-top: 10px;
    text-align: centre;
    font-family: "didot", serif;
}

.footer {
    background-image: linear-gradient(#013440);
    padding: 20px 0;
    /*background-color: #f2f2f2;*/
    width: 100%;
    /*color: #E9EFF2;*/  
}

.footer-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.footer .social-media {
    display: flex;
    gap: 15px;
    margin-bottom: 10px;
    transition: color 0.2s ease;
}

.footer .social-media a {
    color: #656c6d;
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    width: 40px;
    height: 40px;
    background: transparent;
    text-decoration: none;
    z-index: 1;
    overflow: hidden;
    transition: 0.5s;
}

.footer .social-media a:hover {
    color: #D9669B;
}

.footer .copyright {
    font-size: 1.2rem;
    color: #bec2c3;
    font-family: "didot", serif;
    /*font-family: "humanist", sans-serif;*/
}




/* Base styles */
html {
    font-size: 62.5%; /* This makes 1rem = 10px for easier calculations */
}

body {
    font-size: 1.6rem; /* This sets the base font size to 16px */
}

@media (max-width: 480px) {
  .logo img {
    max-height: 32px;
  }
}

@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 60px;
  }

  .header ul {
    flex-direction: row;
  }

  .welcome h1 {
    font-size: 3.6rem;
  }

  .projects-grid {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .project {
    width: 250px;
  }

  .footer-content {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 40px;
  }
}
.footer {
    text-align: center;
}

@media (min-width: 1024px) {
  .welcome h1 {
    font-size: 4.5rem;
  }

  .welcome p {
    font-size: 1.8rem;
  }
}

@media (max-width: 768px) {
  .header-inner {
    flex-direction: column;
    align-items: flex-start;
  }
  .menu-toggle {
    display: block;
    position: absolute;
    align-self: flex-end;
    top: 15px;
    right: 20px;
  }

  .header {
    position: relative;
    flex-direction: column;
    align-items: flex-start;
  }

  .header ul {
    display: none;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    background: #013440;
    padding: 10px 0;
  }

  .header ul.show {
    display: flex;
  }

  .footer-content {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
}